<template>
  <div class="set_nickname">
    <div class="title">订单号验证</div>
    <van-cell-group>
      <van-field v-model="shopName" label="所属店铺" placeholder="请选择订单所属店铺" readonly @click="selectShops"/>
      <van-field v-model="orderNo" type="number" label="订单号" placeholder="请输入订单号" :error="!!$vuelidation.error('orderNo')"/>
    </van-cell-group>

    <div class="bottom_btn">
      <van-button size="large" type="danger" @click="verifyOrder">提交验证</van-button>
    </div>

  </div>
</template>


<script>
  import{Cell, CellGroup, Popup, Picker, Button, Field, Search, Toast} from 'vant';
  import { VERIFY_REGISTER } from '@/api/user';

  export default {

    components:{
      [CellGroup.name]:CellGroup,
      [Cell.name]:Cell,
      [Popup.name]:Popup,
      [Picker.name]:Picker,
      [Button.name]:Button,
      [Field.name]: Field,
      [Search.name]: Search,
      [Toast.name]: Toast
    },

    data(){
      return {
        shopId: '',
        shopName : '',
        orderNo : '',
        taobaoUserId: ''
      }
    },

    computed: {

    },

    vuelidation: {
      data: {
        shopName: {
          required: {
            msg(){
              return "请填写店铺"
            }
          }
        },
        orderNo: {
          required: {
            msg(){
              return "请填写订单号"
            }
          }
        }
      }
    },

    created(){
      this.$bus.$on('selected', (id, name) => {
        this.shopName = name;
        this.shopId = id;
      });
    },

    methods: {

      selectShops(value, index){
        this.$router.push({ name: "shops" });
      },

      verifyOrder(){
        if(!this.shopId){
          Toast.fail("请选择店铺");
          return;
        }
        if(!this.orderNo){
          Toast.fail("请输入订单号");
          return;
        }
        this.$reqPost(VERIFY_REGISTER, {
          orderNo : this.orderNo,
          shopId: this.shopId
        }).then((res) => {
          this.success(res);
        }).catch((err) => {
          // console.log(err)
        })
      },

      success(res){
        if(res.data.code != 0){
          Toast.fail(res.data.msg);
          return;
        }
        this.taobaoUserId = res.data.data.taobaoUserId
        this.orderNo = res.data.data.orderNo
        this.$router.push({name: 'bindInfo', params: {taobaoUserId: this.taobaoUserId, orderNo: this.orderNo, shopId:this.shopId}});
      }
    }
  }
</script>


<style scoped>
  .title{
    padding: 15px;
    background-color: #fff;
  }
  .bottom_btn{
    padding: 30px 15px 0 15px;
  }
</style>



